
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import {API,getQuery} from '../../services'

export default {
components: {
XHeader,
Cell,
Group
},
data () {
return {
storeName:null,
storeFace:null,
storebgPic:null,
address:null,
time:null,
storeId:null,
}
},

methods:{
routerBack(){
this.$router.goBack();
},
/* 数据初始化 */
Initialization(){
this.storeId=this.$route.params.id;
API.main.storeInfo({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.storeName=res.body.data.storeName;
this.storeFace=res.body.data.storeFace;
this.storebgPic=res.body.data.storebgPic;
this.address=res.body.data.enterprise_license_location;
this.time= dateFormat(res.body.data.store_open_time*1000);
}
});

}
},mounted(){
this.Initialization();
},
activated(){
this.Initialization();
}
}



